<template>
  <div class='Templatecompletes' style="clear:both;margin-top: 20px;">
    <div class="main" >
      <div class='step' style="width: 720px;">  <!-- -->
        <ul>
          <li class="active"><i class='el-icon-document'></i><b></b></li>
          <p></p>
          <li class="active"><i class='el-icon-goods'></i><b></b></li>
          <p></p>
          <li class="active"><i class='el-icon-edit'></i><b></b></li>
          <p></p>
          <li class="active"><i class='el-icon-menu'></i><b></b></li>
          <p></p>
          <li class="active"><i class='el-icon-check'></i><b></b></li>
        </ul>
      </div>
      <!-- or -->
      <div class="state">
        <div class="stateInfo">
          <div>
            <img src="../../../../static/images/contract-success.png" alt="" style="margin-top: 40px;">
          </div>
          <div style="margin-left: 30px;">
            <span>,</span>

            <p style="margin-top: 30px;"></p>
          </div>
        </div>
      </div>
      <!--  -->
      <div class='contractInfo'>

        </div>
        <div class='btns'>
            <el-button @click="goHome" type="primary" style='width:200px'></el-button>
        </div>
    </div>
  </div>
</template>

<style scoped>
  @import "../../../styles/Templatecomplete/Templatecompletes.css";
  @import "../../../common/styles/SigningSteps.css";
</style>

<script>
import {prohibit} from '@/common/js/prohibitBrowser'
export default {
    mounted() {
        prohibit()
        sessionStorage.removeItem("type");
    },
    methods:{
        goHome(){
            this.$store.dispatch('tabIndex',{tabIndex:0});
                this.$router.push('/Home')

        }
    }

}
</script>



