<template>
  <div>
    <div class="Topes" style="position: fixed;top:0;z-index: 999">
      <nav class='nav'>
        <p class='logo'>
          <img src="../../../../static/images/logo2.png" alt="">
        </p>
        <div class='buttons'>
          <el-button type="info" style="background:#ccc" @click="cancelSign">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</el-button>
          <el-button style="color:#4091fb" @click="entCertification" >&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</el-button>
        </div>
      </nav>
    </div>
    <div class='Enterprises' style="margin-top: 100px;z-index: 9;">
      <div class='step' style="width:720px;">
        <ul>
          <li class="active"><i class='el-icon-document'></i><b></b></li>
          <p></p>
          <li class="active"><i class='el-icon-goods'></i><b></b></li>
          <p></p>
          <li class="active"><i class='el-icon-edit'></i><b></b></li>
          <p></p>
          <li ><i class='el-icon-menu'></i><b></b></li>
          <p></p>
          <li ><i class='el-icon-check'></i><b></b></li>
        </ul>
      </div>
      <div class='productes'>
        <h2 class='title' style='padding-left:15px;padding-top: 20px;'>
          <img src="../../../../static/images/Confirmation/Enterprise/title.jpg" alt="">
        </h2>
        <div class='center' style="background:#fff">
          <div class='container'>
            <div class='firstSelect'>
              <span></span>
              <el-select v-model="enterpriseType" slot="prepend" placeholder="" style='width:336px;height:20px;'>
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
                <el-option label="\" value="3"></el-option>
                <el-option label="" value="4"></el-option>
                <el-option label="\" value="5"></el-option>
              </el-select>
            </div>
            <div class='second'>
              <span></span>
              <el-input
                style='width:336px;height:20px;'
                placeholder=""
                v-model="tenantName"
                @blur='enterpriseName'
                :disabled= fullName
              >
              </el-input>

              <p class='secondTitle' style="text-align:left"></p>
            </div>
            <div class='second'>
              <span style='margin-left:-53px;'></span>
              <el-input
                style='width:336px;height:20px;'
                placeholder=""
                v-model="creditCode"
                :maxlength=25
              >
              </el-input>
              <p class='secondTitle' style="color:#ff0040;"></p>
            </div>
            <div class='third' style="margin-top:20px;">
              <span class='thirdInfo'>/</span>
              <el-input
                style="width:336px;height:20px;margin-left:11px;"
                placeholder=""
                v-model="legalPerson"
                :maxlength=15
              >
              </el-input>
              <p class='secondTitle'>,,</p>
              <a href="javascript:void(0)" class='check' @click="dialogAgreement = true"></a>
              <el-dialog
                :visible.sync="dialogAgreement"
                width="500px"
              >
                <img src="../../../../static/images/case.png" alt="" style="width:100%;height:100%;">
              </el-dialog>
            </div>
            <div class='four' style="clear: both;">
              <span  class='fourInfo' style='float:left'></span>
              <div  style='float:left'>

                <div class="upload_warp">
                  <div class="upload_warp_left " @click="fileClick" style="font-size:0;position:relative" >
                    <i class='el-icon-plus' style="font-size:60px;display:block;position:absolute;left:0;right:0;top:60px;bottom:0;" id='firstFile'></i>
                    <img :src="imgList.src" style="width:100%;height:100%;border:none;" v-show="imgList.src!=null">
                  </div>
                </div>
                <div class="upload_warp_text">
                  {{bytesToSize(this.size)}}
                </div>
                <input @change="fileChange($event)" type="file" id="upload_file" multiple style="display: none"/>

              </div>
            </div>
            <div style='clear:both' class='person'>
              <div class='personInfo' style='float:left;margin-left:25%;'></div>
              <el-radio v-model="radio" label="1" style='float:left;padding-top: 5px;'></el-radio>
              <el-radio v-model="radio" label="2" style='float:left;padding-top: 5px;'></el-radio>
              <a href="javascript:void(0)" class='check' style='visibility: hidden;'></a>
            </div>
            <div class='four' style="clear: both;">
              <span  class='fourInfo' style='float:left'></span>
              <div class='11221' style='float:left'>

                <div class="upload_warp">
                  <div class="upload_warp_left" @click="Uploadclick" style="font-size:0;position:relative">
                    <i class='el-icon-plus' style="font-size:60px;display:block;position:absolute;left:0;right:0;top:60px;bottom:0;" id='secondFile'></i>
                    <img :src="proxyImg.src" style="width:100%;height:100%;border:none;" v-show="proxyImg.src!=null">
                  </div>
                </div>
                <div class="upload_warp_text">
                  {{UploadBytesToSize(this.fileSize)}}
                </div>
                <input @change="UploadChange($event)" type="file" id="upload_proxy" multiple style="display: none"/>

              </div>
            </div>
            <div class='agreement' style='clear:both'>
              <p><a href='javascript:void(0);' @click="coveringLetter"></a></p>
              <p><a href='javascript:void(0);' @click='authorization'></a></p>
              <p><a href='javascript:void(0);' @click="letterAttorney"></a></p>
              <p></p>
              <p>.jpg .jpeg .png2M.</p>
            </div>
          </div>
        </div>

      </div>
    </div>
  </div>
</template>
<script>
  import { Message } from 'element-ui';
  import { validateCredit,validateOpenName, TrimAll} from '@/common/js/validate'
  export default {
    name: 'Enterprises',
    data() {
      return {

        tenantName:'',
        enterpriseType:'',
        creditCode:'',
        legalPerson:'',
        authorizerType:'',
        creditPhoto:'',
        authorizationPhoto:'',
        imgList: '',
        proxyImg:'',
        size: 0,
        fileSize:0,
        dialogAgreement:false,
        radio:'1',
        fullName:true,
        disabled:false
      }
    },
    created() {
      var enterpriseName = sessionStorage.getItem('enterpriseName')
      this.tenantName = enterpriseName
    },
    methods: {
      enterpriseName() {
        if(this.input10 ==''){
          //
        }
      },
      creditInfo() {
        var reg = /^[0-9a-zA-Z]+$/
        if(TrimAll(this.creditCode) == ''){
          // this.$alert('','', {
          //   confirmButtonText: ''
          // });
          this.$message({
            showClose: true,
            message: '',
            type: 'warning'
          })
          return false
        }else if(TrimAll(this.creditCode) != '' && !reg.test(TrimAll(this.creditCode))){
          // this.$alert('','', {
          //   confirmButtonText: ''
          // });
          this.$message({
            showClose: true,
            message: '',
            type: 'warning'
          })
          return false
        }
      },
      nonnumeric() {
        if(this.legalPerson == ''){
          // this.$alert('/','', {
          //   confirmButtonText: ''
          // })
          this.$message({
            showClose: true,
            message: '/',
            type: 'warning'
          })
          return false
        }else if(TrimAll(this.legalPerson) != '' && !validateOpenName(TrimAll(this.legalPerson))){
          // this.$alert('/','', {
          //   confirmButtonText: ''
          // })
           this.$message({
            showClose: true,
            message: '/',
            type: 'warning'
          })
          return false
        }
      },
      cancelSign() {    //
        const h = this.$createElement;
        this.$msgbox({
          title: '',
          message: h('p', null, [
            h('span', null, ' '),
            h('i', { style: 'color: teal' }, '')
          ]),
          showCancelButton: true,
          confirmButtonText: '',
          cancelButtonText: '',
          beforeClose: (action, instance, done) => {
            if (action === 'confirm') {
              instance.confirmButtonLoading = true;
              instance.confirmButtonText = '...';
              setTimeout(() => {
                done();
                this.$router.push('/')
                setTimeout(() => {
                  instance.confirmButtonLoading = false;
                }, 50);
              }, 100);
            } else {
              done();
            }
          }
        })
      },
      //
      fileClick() {
        document.getElementById('upload_file').click()
      },
      Uploadclick() {
        document.getElementById('upload_proxy').click()
      },
      fileChange(el) {
        if (!el.target.files[0].size) return;
        this.fileList(el.target);
        el.target.value = ''
      },
      UploadChange(el) {
        if (!el.target.files[0].size) return;
        this.UploadList(el.target);
        el.target.value = ''
      },
      fileList(fileList) {
        let files = fileList.files;
        for (let i = 0; i < files.length; i++) {
          //
          if (files[i].type != '') {
            this.fileAdd(files[i]);
          } else {
            //
            if(fileList.items){
              this.folders(fileList.items[i]);
            }else{
              this.$alert('!','', {
                confirmButtonText: ''
              });
            }
          }
        }
      },
      UploadList(UploadList) {
        let files = UploadList.files;
        for (let i = 0; i < files.length; i++) {
          //
          if (files[i].type != '') {
            this.UploadFileAdd(files[i]);
          } else {
            //
            if(UploadList.items){
              this.UploadFolders(UploadList.items[i]);
            }else{
              this.$alert('!','', {
                confirmButtonText: ''
              });
            }
          }
        }
      },
      //
      folders(files) {
        let _this = this;
        //file
        if (files.kind) {
          files = files.webkitGetAsEntry();
        }
        files.createReader().readEntries(function (file) {
          for (let i = 0; i < file.length; i++) {
            if (file[i].isFile) {
              _this.foldersAdd(file[i]);
            } else {
              _this.folders(file[i]);
            }
          }
        })
      },
      //
      UploadFolders(files) {
        let _this = this;
        //file
        if (files.kind) {
          files = files.webkitGetAsEntry();
        }
        files.createReader().readEntries(function (file) {
          for (let i = 0; i < file.length; i++) {
            if (file[i].isFile) {
              _this.UploadFoldersAdd(file[i]);
            } else {
              _this.UploadFolders(file[i]);
            }
          }
        })
      },
      foldersAdd(entry) {
        let _this = this;
        entry.file(function (file) {
          _this.fileAdd(file)
        })
      },

      UploadFoldersAdd(entry) {
        let _this = this;
        entry.file(function (file) {
          _this.UploadFileAdd(file)
        })
      },
      //base64 
      fileAdd(file) {
        if (this.limit !== undefined) this.limit--;
        if (this.limit !== undefined && this.limit < 0) return;
        //

        //
        var reg= /[.](png|PNG|jpg|JPG|jpeg|JPEG)$/
        var index = file.name.lastIndexOf('.')
        if (!reg.test(file.name.slice(index))) {
          this.$alert('!','', {
            confirmButtonText: ''
          });
          return false
        }else if(file.size > 2*1024*1024){
          this.$alert('','', {
            confirmButtonText: ''
          });
          return false
        } else {
          document.getElementById('firstFile').style.display='none';
          this.size = file.size;
          let reader = new FileReader();
          let image = new Image();
          let _this=this;
          reader.readAsDataURL(file);
          reader.onload = function () {
            file.src = this.result;
            image.onload=function(){
              let width = image.width;
              let height = image.height;
              file.width=width;
              file.height=height;
              _this.imgList=file
            };
            image.src= file.src;

            _this.$http.post(process.env.API_HOST+'v1.4/async/imgUpload',{'image':file.src},{emulateJSON: true}).then(res =>{
              if(res.data.resultCode == '1'){
                _this.$message({
                  showClose: true,
                  message: res.data.resultMessage,
                  type: 'success'
                })
                _this.creditPhoto = res.data.data
              } else {
                _this.$message({
                  showClose: true,
                  message: res.data.resultMessage,
                  type: 'error'
                })
              }
            })
          }
        }
        // 
      },
      UploadFileAdd(file) {
        if (this.limit !== undefined) this.limit--;
        if (this.limit !== undefined && this.limit < 0) return;

        //
        var reg= /[.](png|PNG|jpg|JPG|jpeg|JPEG)$/
        var index = file.name.lastIndexOf('.')
        if (!reg.test(file.name.slice(index))) {
          this.$alert('!','', {
            confirmButtonText: ''
          });
          return false
        } else if(file.size > 2*1024*1024){
          this.$alert('','', {
            confirmButtonText: ''
          });
          return false
        }else {
          //
          document.getElementById('secondFile').style.display='none';
          this.fileSize =  file.size;
          let reader = new FileReader();
          let image = new Image();
          let _this=this;
          reader.readAsDataURL(file);
          reader.onload = function () {
            file.src = this.result;
            image.onload=function(){
              let width = image.width;
              let height = image.height;
              file.width=width;
              file.height=height;
              _this.proxyImg=file
            };
            image.src= file.src;

            _this.$http.post(process.env.API_HOST+'v1.4/async/imgUpload',{'image':file.src},{emulateJSON: true}).then(res =>{
              if(res.data.resultCode == '1'){
                _this.$message({
                  showClose: true,
                  message: res.data.resultMessage,
                  type: 'success'
                })
                _this.authorizationPhoto = res.data.data
              } else {
                _this.$message({
                  showClose: true,
                  message: res.data.resultMessage,
                  type: 'error'
                })
              }
            })
          }
        }
      },
      //
      fileDel(index) {
        this.size = this.size - this.imgList[index].file.size;//
        this.imgList.splice(index, 1);
        if (this.limit !== undefined) this.limit = this.imgList.length;
      },
      //
      bytesToSize(bytes) {
        if (bytes === 0) return '0 B';
        let k = 1024,
          sizes = ['B', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'],
          i = Math.floor(Math.log(bytes) / Math.log(k));
        return (bytes / Math.pow(k, i)).toPrecision(3) + ' ' + sizes[i];
      },
      UploadBytesToSize(bytes) {
        if (bytes === 0) return '0 B';
        let k = 1024,
          sizes = ['B', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'],
          i = Math.floor(Math.log(bytes) / Math.log(k));
        return (bytes / Math.pow(k, i)).toPrecision(3) + ' ' + sizes[i];
      },
      dragenter(el) {
        //
        el.stopPropagation();
        el.preventDefault();
      },
      dragover(el) {
        //
        el.stopPropagation();
        el.preventDefault();
      },
      drop(el) {
        //
        el.stopPropagation();
        el.preventDefault();
        this.fileList(el.dataTransfer);
      },

      //
      entCertification(){
        let that = this;
        // that.disabled=true;
        if(this.enterpriseType ==''){
           this.$message({
            showClose: true,
            message: '',
            type: 'warning'
          })

          return false
        }
        if(this.radio == '1'){
          this.authorizerType = '1'
        }else {
          this.authorizerType = '0'
        }
        if(this.creditInfo() == false){ //
          return false
        }
        if(this.nonnumeric() == false){
          return false
        }
        if(this.creditPhoto == ''){
          // this.$alert('','', {
          //   confirmButtonText: ''
          // });
          this.$message({
            showClose: true,
            message: '',
            type: 'warning'
          })
          return false
        }
        if(this.authorizationPhoto == ''){
          // this.$alert('','', {
          //   confirmButtonText: ''
          // });
          this.$message({
            showClose: true,
            message: '',
            type: 'warning'
          })
          return false
        }

        var interfaceCode = sessionStorage.getItem('interfaceCode')
        //    interfaceCode = JSON.parse(interfaceCode)
        that.$http.post(process.env.API_HOST+'v1.4/tenant/'+interfaceCode+'/authentication',{
          'tenantName':this.tenantName,//
          'enterpriseType':this.enterpriseType,//
          'creditCode':this.creditCode,//
          'legalPerson':this.legalPerson,//
          'creditPhoto':this.creditPhoto,//
          'authorizerType':this.authorizerType,//(01)
          'authorizationPhoto':this.authorizationPhoto//
        },{emulateJSON: true}).then(res =>{
          if (res.data.resultCode == '1') {
            that.$message({
              showClose: true,
              message: res.data.resultMessage,
              type: 'success'
            })
            this.$router.push('/Payment')
          } else {
            that.$message({
              showClose: true,
              message: res.data.resultMessage,
              type: 'warning'
            })
          }
        })
      },
      coveringLetter (){
        var way = '1'
        var url = process.env.API_HOST+'v1.4/tenant/downloadFile?fileType='+way;
        var up = document.createElement('a');
        document.body.appendChild(up)
        up.setAttribute('href',url);
        up.click()
      },
      authorization () {
        var way = '2'
        var url = process.env.API_HOST+'v1.4/tenant/downloadFile?fileType='+way;
        var up = document.createElement('a');
        document.body.appendChild(up)
        up.setAttribute('href',url);
        up.click()
      },
      letterAttorney() {
        var way = '3'
        var url = process.env.API_HOST+'v1.4/tenant/downloadFile?fileType='+way;
        var up = document.createElement('a');
        document.body.appendChild(up)
        up.setAttribute('href',url);
        up.click()
      }
    }
  }
</script>
<style scoped>
  @import "../../../styles/Confirmation/Enterprise/Enterprises.css";
  @import "../../../common/styles/Topes.css";
  @import "../../../common/styles/SigningSteps.css";

</style>



