import request from '../utils/fetch'
const service = {
  
    //
    license(param) {
        return request({
            url: '/api/v1.6/tenant/creditPhotoOcr',
            method: 'post',
            params:param
        })
    },
    //
    valiteMobile(param) {
        return request({
            url: '/api/v1/tenant',
            method: 'get',
            params:param
        })
    },
    //
    smsCode(param) {
        return request({
            url: '/api/v1.4/sms/sendCode',
            method: 'post',
            params:param
        })
    },
    //
    smsValite(param) {
        return request({
            url: '/api/v1.4/sms',
            method: 'get',
            params:param
        })
    },
    //
    licenseInfo(param) {
        return request({
            url: '/api/v1.6/tenant/authentication',
            method: 'post',
            params:param
        })
    },
     //
     IdCardInfo(param) {
        return request({
            url: '/api/v1.6/user/authentication',
            method: 'post',
            params:param
        })
    },
    //
    bankInfo(param,interfaceCode) {
        return request({
            url: '/api/v1.4/tenant/'+interfaceCode+'/bank',
            method: 'post',
            params:param
        })
    },
    //v1.6/tenant/{interfaceCode}/tenantDetails
    companyInfoDetail(param,interfaceCode) {
        return request({
            url: '/api/v1.6/tenant/'+interfaceCode+'/tenantDetails',
            method: 'get',
            params:param
        })
    },
      //
      login(param, urlParam) {
        return request({
        url: '/api/v1.4/tenant/' + urlParam + '/homePage',
        method: 'get',
        params: param
        })
    },
}
export default service;





