                  </li>
                  <li>
                    <span><span style="padding: 0 22px;"></span>:</span>
                    <b class="state" v-if="item.accountStatus=='0'"></b>
                    <b class="state" v-if="item.accountStatus=='1'"></b>
                    <b class="state" v-if="item.accountStatus=='2'"></b>
                    <b class="state" v-if="item.accountStatus=='3'"></b>
                    <b class="state" v-if="item.accountStatus=='4'"></b>
                    <b class="state" v-if="item.accountStatus=='5'"></b>
                    <b class="state" v-if="item.accountStatus=='6'"></b>
                  </li>
                  <li>
                    <span>
                      <span style="padding: 0 3px;"></span>
                      <span style="padding: 0 1px;"></span>
                      <span style="padding: 0 3px;"></span>:</span>
