<!--->-->
<template>
  <div class="AddChildAccounts">
    <div class="main" >
      <div class="container">
        <div class="content">
          <div class="content-body">

            <p class="title"></p>
            <span class="new-child-account">()</span>
            <div class="border-bottom"  style="margin-top: 0"></div>

            <div class="account-fill" >

              <div class="show-info-list">
                <p style="font-size: 12px;color: red;padding-left: 30px;margin-bottom: 20px;"></p>
                <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="account-ruleForm" size="medium">

                  <el-form-item label="" :label-width="formLabelWidth" prop="userName">
                    <el-input v-model="ruleForm.userName" auto-complete="off" placeholder="" :maxlength= 10></el-input>
                  </el-form-item>

                  <el-form-item label="" :label-width="formLabelWidth" prop="accountName">
                    <el-input v-model="ruleForm.accountName" auto-complete="off" placeholder="" :maxlength= 15></el-input>
                  </el-form-item>

                  <el-form-item label="" :label-width="formLabelWidth" prop="mobile">
                    <el-input v-model="ruleForm.mobile" auto-complete="off" placeholder="" :maxlength= 11 :minlength= 11></el-input>
                  </el-form-item>



                  <el-form-item label="" :label-width="formLabelWidth" prop="password">
                    <el-input v-model="ruleForm.password" type="password" auto-complete="off" placeholder="" :minlength= 8 :maxlength= 16 :disabled="dis" id="password"></el-input>

                    <el-tooltip class="item" effect="dark" content="" placement="right"  v-show="showToolTip">
                      <el-button style="position: absolute;top:-3px;right:-4px;border:none;padding: 10px;"><i class="el-icon-question" style="font-size: 25px;color: red;"></i></el-button>
                    </el-tooltip>
                  </el-form-item>


                  <el-form-item label="" :label-width="formLabelWidth" prop="idCode">
                    <el-input v-model="ruleForm.idCode" auto-complete="off" placeholder="" ></el-input>
                  </el-form-item>


                  <el-form-item label="" :label-width="formLabelWidth" prop="Email">
                    <el-input v-model="ruleForm.Email" auto-complete="off" placeholder="" ></el-input>
                  </el-form-item>

                </el-form>
              </div>

            </div>
          </div>

          <div class="content-body" style="background: #fff;padding-bottom:50px;margin-bottom: 35px;">
            <p class="title"></p>
            <span class="new-child-account" >()</span>
            <div class="border-bottom" style="margin-top: 0;"></div>

            <div class="child-template" >
              <div class="child-template-list" >

                <div class="single-list" v-if="(singleTemplateLength)||(batchTemplateLength)">

                  <h3></h3>
                  <template>
                    <el-checkbox-group v-model="singleTemplate" >
                      <el-checkbox v-for="item in single" :label="item.templateNo" :key="item.templateNo">{{item.name}}</el-checkbox>

                    </el-checkbox-group>

                  </template>


                </div>



                <div class="batch-list" v-if="(singleTemplateLength)||(batchTemplateLength)">
                  <h3></h3>
                  <template>
                    <el-checkbox-group v-model="batchTemplate" >
                      <el-checkbox v-for="item in batch" :label="item.templateNo"   :key="item.templateNo">{{item.name}}</el-checkbox>
                    </el-checkbox-group>


                  </template>



                </div>
                <div class="no-template" v-if="(!singleTemplateLength)&&(!batchTemplateLength)">
                  <img src="../../../../static/images/Account/no-template.png">
                  <p style="color: #999;"></p>
                </div>

                <div class="fill-background"></div>

                <!--<div class="empty-template" >-->
                <!--<img src="../../../../../static/images/Confirmation/Account/default-template.png">-->
                <!--<p class="tips"></p>-->
                <!--</div>-->
              </div>


            </div>

            <div class="agreement-content" @change="changEvent">
              <template>
                <!-- `checked`  true  false -->
                <el-checkbox v-model="agree" name="type" ></el-checkbox>
                <b  class="agreement-sign">
                  <!--<a  @click="dialogAgreement = true" href="javascript:void(0)" ></a>-->
                  <a  @click="dialogAgreement = true" href="javascript:void(0)" ></a>
                </b>
              </template>
              <el-dialog
                :visible.sync="dialogAgreement"
                width="30%"
                center>
                <div class="dialog-container">

                  <div class="dialog-body">
                    <div class="content">

                      <p style="text-align: center;font-size: 16px;font-weight: bold;"></p>

                      <br/>
                      <p></p>

                      <p><span style="padding:0 10px;"></span>{{ruleForm.userName}}            {{ruleForm.idCode}}                   </p>

                      <p> 1)</p>

                      <p> 2)</p>

                      <p></p>
                      <br/>
                      <p></p>
                      <br/>
                      <p> {{enterpriseName}}</p>
                      <br/>
                      <p>     :{{date}}</p>

                    </div>
                  </div>

                </div>

              </el-dialog>

              <div class="operate-buttons">
                <button class="quit" @click="quit('ruleForm')" href="javascript:void(0)">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</button>
                <button class="submit"  @click="submitBtn('ruleForm')" href="javascript:void(0)" :disabled="once"></button>
              </div>
            </div>
          </div>
        </div>

      </div>
    </div>

  </div>
</template>
<script>

  import Account from "../Account"    //
  import md5 from 'js-md5'
  import {validateMoblie,validateEmail,TrimAll,validatePassWord,validateCard} from '@/common/js/validate'
  import cookie from '@/common/js/getTenant'
  import server from "@/api/url";
  import EditChildNoActive from "../EditChildNoActive/EditChildNoActive" //
  export default {
    name: 'AddChildAccounts',
    component:{
      Account,
      EditChildNoActive
    },
    data() {
      // 
      var validateAccountName = (rule,value,callback) => {
        if (TrimAll(value) === ''){
          callback(new Error(''))
        } else if (value.length<2 || value.length > 15 ) {
          callback(new Error('2-15'))
        } else {
          callback()
        }
      }
      // 
      var validateUserName = (rule,value,callback) => {
        if (TrimAll(value) === ''){
          callback(new Error(''))
        } else if (value.length<2 || value.length > 15 ) {
          callback(new Error(''))
        } else {
          callback()
        }
      }
      //
      var validateIdCard = (rule,value,callback) => {
        if (TrimAll(value) === ''){
          callback(new Error(''))
        } else if (value !== '' && !validateCard(value)){
          callback(new Error(''))
        } else {
          callback()
        }
      }
      // 
      var validateChildPassWord = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value.length < 8 || value.length > 16) {
          callback(new Error('8-16'))
        } else if (!validatePassWord(value)){
          callback(new Error('+'))
        }else {
          callback();
        }
      }
      // 
      var validateChildMobile = (rule,value,callback) => {

        if(value === ''){
          callback(new Error(''))
        } else if (!validateMoblie(value)){
          callback(new Error(''))
        }else if(value==this.mobile){
          this.showToolTip=false;
          this.ruleForm.password='';
          callback(new Error(''))
        } else {
          let params = {
            username: this.ruleForm.mobile
          };

            server.verficate(params).then(res => {
              if (res.data === 0) {

                document.getElementById('password').focus()
                this.ruleForm.password="test111111";
                this.dis=true;
                this.showToolTip=true;

              } else {
                this.showToolTip=false;
                this.dis=false;
                this.ruleForm.password='';

              }
            }).catch(error => {

            })

          callback()
        }
      }
      //
      var validateChildEmail=(rule,value,callback)=>{
        if (value === '') {
          callback(new Error(''));
        }else if (value !== '' && !validateEmail(value)){
          callback(new Error(''))
        } else{
          callback()
        }
      }

      return{
        server:true,
        ruleForm: {
          accountName:'',  //
          userName:'',//
          idCode:'', //
          mobile:'' ,//
          password:'', //
          Email:'',  //
        },
        formLabelWidth:'120px',
        checkAll: false,
        singleTemplate: [],
        batchTemplate: [],
        single: [],
        batch: [],
        isIndeterminate: true,
        agree:false,  //
        dialogAgreement:false, //,
        interfaceCode:cookie.getJSON("tenant")[1].interfaceCode, //cookieinterfaceCode
        dis:false,
        enterpriseName:sessionStorage.getItem("enterpriseName"),  //
        rules:{
          accountName: [
            { required: true, validator: validateAccountName, trigger: 'blur' }
          ],
          userName: [
            { required: true, validator: validateUserName, trigger: 'blur' }
          ],
          password: [
            { required: true, validator: validateChildPassWord, trigger: 'blur' }
          ],
          Email:[
            {required:true,validator:validateChildEmail,trigger: 'blur'}
          ],
          idCode:[
            { required: true,validator: validateIdCard, trigger: 'blur'}
          ],
          mobile:[
            { required: true, validator: validateChildMobile, trigger: 'blur' }
          ],
        },
        once:false , //
        date:'' ,//
        showToolTip:false,
        singleTemplateLength:false, //
        batchTemplateLength:false,     //
        fullscreenLoading: false,
        mobile:sessionStorage.getItem("mobile"),

      }
    },
    methods: {
        //
      changEvent(){
        this.$http.get(process.env.API_HOST + "v1.5/user/getDate").then(function(res) {
        //   console.log(res.bodyText)
          this.date=res.bodyText;
        });
      },

      // 
      quit(formName){
        this.$refs[formName].resetFields();
        this.$router.push('/Account');
      },

      //
      submitBtn(formName){

        let enterpriseName= sessionStorage.getItem("enterpriseName");
        if(this.agree){
          this.$refs[formName].validate((valid) => {
            if (valid) {

              this.$nextTick(function () {
                this.$loading.show();
              });
              this.once=true;//
              let pass = md5(this.ruleForm.password); //MD5
              let batchTemplate=JSON.stringify(this.batchTemplate);  //
              let singleTemplate=JSON.stringify(this.singleTemplate);  //

              // let batchTemplate1=batchTemplate.substr(2,batchTemplate.length-3);
              let batchTemplate1=batchTemplate.replace("[",",").replace("]","").replace(/\"/g,"");
              let singleTemplate1=singleTemplate.replace("[",",").replace("]","").replace(/\"/g,"");
              let templates=(batchTemplate1+singleTemplate1).substr(1);
              let manageName=sessionStorage.getItem("authName")
              if((this.batchTemplate.length+this.singleTemplate.length)=='1'){
                templates=templates.replace(",", "");
              }
              if((this.batchTemplate.length+this.singleTemplate.length)=='0'){
                templates='';

              }
              this.$http.post(process.env.API_HOST+'v1.5/tenant/'+this.interfaceCode+'/addAccount',{
                accountName:this.ruleForm.accountName ,  //  
                userName:this.ruleForm.userName,            //
                idCard:this.ruleForm.idCode,                  //
                mobile:this.ruleForm.mobile ,              //
                password:pass,                 //
                email:this.ruleForm.Email,                    //
                templates:templates,                                //
                company_name:enterpriseName,             //
                manageName:manageName,
              },{emulateJSON: true}).then(res =>{
                let accountCode=res.data.accountCode;  //accountCode
                this.fullscreenLoading = true;
                setTimeout(() => {
                  this.fullscreenLoading = false;
                }, 2000);
                //
                if(res.data.resultCode=='1'){

                  this.$message({
                    showClose: true,
                    message:res.data.resultMessage,
                    type: 'success'
                  })
                    this.$nextTick(function () {
                        this.$loading.hide();
                    });
                    this.$router.push("/Account");
                }else if(res.data.resultCode=='0'){
                  //   
                  sessionStorage.setItem('subAccountCode',res.data.data.accountCode)

                    this.$nextTick(function () {
                        this.$loading.hide();
                    });
                    // this.$message({
                    //     showClose: true,
                    //     message:res.data.resultMessage,
                    //     type: 'error'
                    // });
                  let num = 3;
                  if(res.data.data){
                    num = num-res.data.data.authNum;
                    if(num>='1'){
                      this.$alert(<div style="textAlign:center">
                        <p></p>
                       <p class="vertifiId-warn warn-first"></p>
                        <p class="vertifiId-warn">{num}</p> </div>, '',{confirmButtonText: '',});

                    }else{
                      this.$alert(<div style="textAlign:center">
                        <p></p>
                      <p class="vertifiId-warn warn-first"></p>
                        </div>, '',{confirmButtonText: '',});}
                    }
                    this.$router.push('/EditChildNoActive')
                  }else if(res.data.resultCode=='2'){
                   this.once=false;
                  //
                    this.$nextTick(function () {
                        this.$loading.hide();
                    });
                    this.$message({
                        showClose: true,
                        message:res.data.resultMessage,
                        type: 'error'
                    })

                }
              })
            }else{
              this.$message({
                showClose: true,
                message: '!',
                type: 'error'
              })
            }

          })

        }else{
          this.$alert('!', '',{
            confirmButtonText: ''
          });
          return false
        }

      },

    },
    created() {
      this.$http.get(process.env.API_HOST + "v1/tenant/"+this.interfaceCode + "/templateList").then(function(res) {
        let data=res.data;
        let batchArray=[];
        let singleArray=[];
         if(data){
           for(let i=0;i<data.length;i++){

             if(data[i].templateSpecies=='single'){
               singleArray.push(data[i]);
             }else {
               batchArray.push(data[i]);
             }
           }
           this.single=singleArray;
           this.batch=batchArray;
           if(this.single.length==0){
             this.singleTemplateLength=false
           }else{
             this.singleTemplateLength=true
           }
           if(this.batch.length==0){
             this.batchTemplateLength=false
           }else{
             this.batchTemplateLength=true
           }
         }


      });
    }
  }
</script>

<style lang="stylus">
  @import "../../../styles/Account/ChildAccount.styl";
  .single-list,.batch-list{
    width: 470px;
    float: left;
  }
  .batch-list{
    /*border-left: 1px solid #4091fb;*/
  }
  .single-list>h3,.batch-list>h3{
    color: #4091fb;
    margin: 20px 0 10px 20px;
  }
  .vertifiId-warn{
      color:red
  }
  .warn-first{
      margin-top:25px!important;
  }

  .el-checkbox-group>.el-checkbox{
    display:block !important;
    height: 40px !important;

    overflow: hidden!important;
    vertical-align: middle;
    margin-left: 0!important;
  }
  .single-list>.el-checkbox-group{
    padding-left: 20px;
  }
  .batch-list>.el-checkbox-group {
    padding-left: 20px;
    border-left: 1px solid #4091fb;
  }
  .account-ruleForm>.el-form-item{
    width: 525px;
     display: inline-block;
  }


  .account-ruleForm>.el-form-item{
    /*width: 50%;*/
  }
  .account-ruleForm>.el-form-item>.el-form-item__content>.el-form-item__error{
    margin-left: 20px;

  }
  .account-ruleForm>.el-form-item>.el-form-item__content>.el-input>.el-input__inner{
    width:330px;
    margin-left: 20px;
  }
  .content-body>p.title{
    background: url("../../../../static/images/Common/title.png") no-repeat;
  }

  b.agreement-sign{
    color: #424242;
    font-size: 14px;
  }
  b.agreement-sign>a{
    padding-left: 10px;
    color: #4091fb;
    cursor: pointer;
  }
  .content>p{
    line-height: 30px;
  }

</style>



