<template>
  <div class='Informations'>
    <div class="Topes">
      <nav class='nav'>
        <p class='logo'>
          <img src="../../../../../static/images/logo2.png" alt="">
        </p>
      </nav>
    </div>
    <div class='contents'>
     <div class='step' style="width: 720px;">
      <ul>
        <li class="active"><i class='el-icon-document'></i><b></b></li>
        <p></p>
        <li class="active"><i class='el-icon-goods'></i><b></b></li>
        <p></p>
        <li><i class='el-icon-edit'></i><b></b></li>
        <p></p>
        <li><i class='el-icon-menu'></i><b></b></li>
        <p></p>
        <li><i class='el-icon-check'></i><b></b></li>
      </ul>
    </div>
    <div style="width: 100%;border-top: 1px solid #ddd;margin-bottom:20px;"></div>
    <div class='productes'>
      <div class='center'>
        <div class='container'>
          <div class="content-title">
            <ul>
              <li><b></b></li>
              <li><b></b></li>
              <li><b></b></li>
            </ul>
            <p class='personInfo'>
              <!-- <span></span> -->
              <span style='color:#4091fb'></span>
            </p>
          </div>
          <div class="header-title-4" style="padding:20px;background: #fafafa;">
            <div class='personUpload' style="background: #fff;height: 370px;">
              <p class="personUpload-p"><span></span><b></b></p>
              <div class='leftInfo'>
                <h2></h2>
                <p><i style='font-style:normal'></i><b ref='user'>{{name}}</b></p>
                <p><i style='font-style:normal'></i><b ref='idcode'>{{idcard}}</b></p>
              </div>
              <p class='textInfo'>:2MJPEG.JPG.PNG</p>
            </div>



            </div>
          <div class='btns' style='clear:both;width: 740px;margin: 30px auto;'>
            <router-link to='/Pupload'><el-button type="primary" style='width:200px;margin-right:10px;' ></el-button></router-link>
            <el-button type="primary" style='width:200px'  @click='submitInfo' :disabled =enjoin  >&nbsp;&nbsp;</el-button>
          </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style>
 @import "../../../../styles/Credentials/Personal/Information/Informations.css";
 @import "../../../../common/styles/Topes.css";
 @import "../../../../common/styles/SigningSteps.css";
</style>
<script>
  import {prohibit} from '@/common/js/prohibitBrowser'
  export default {
    name: 'Informations',
    data () {
      return {
        name:'',
        idcard:'',
        falg:false,
        enjoin:false,
      }
    },
    mounted() {
      prohibit()
    },
    methods:{
      submitInfo () {
          var userCode =sessionStorage.getItem('userCode')
        //   userCode = JSON.parse(userCode)
          var mobile = sessionStorage.getItem('mobile')
        //   mobile = JSON.parse(mobile)
          var interfaceCode = sessionStorage.getItem('interfaceCode')
        //   interfaceCode = JSON.parse(interfaceCode)
          if(this.falg == false){
          this.falg = true
          this.$http.post(process.env.API_HOST+'v1.4/user/'+userCode+'/authentication',{'userName':this.name,'mobile':mobile,'idCard':this.idcard,'interfaceCode':interfaceCode},{emulateJSON: true}).then(res =>{
            if(res.data.resultCode == '1'){
              this.$message({
                showClose: true,
                message: res.data.resultMessage,
                type: 'success'
              })
              this.$router.push('/Authentication')
            } else {
              this.$message({
                showClose: true,
                message: '',
                type: 'error'
              })
              this.falg = false
              this.enjoin = true
            }
          })
        }
      }
    },
    created() {
      var idcard =sessionStorage.getItem('idcard')
      var name =sessionStorage.getItem('name')
      this.name = name
      this.idcard = idcard
    }
  }
</script>



