<template>
  <div>
    <div class="Topes">
      <nav class='nav'>
        <p class='logo'>
          <img src="../../../../static/images/logo2.png" alt="">
        </p>

      </nav>
    </div>
    <div class="EnterprisePayments">
      <div class="step" style="width:420px;">
        <ul>
          <li class="active"><i class='el-icon-goods'></i><b></b></li>
          <p></p>
          <li class="active"><i class='el-icon-menu'></i><b></b></li>
          <p></p>
          <li><i class='el-icon-check'></i><b></b></li>
        </ul>
      </div>
      <div class="payment-operate">
        <div class="tips">
          <br/>
          
        </div>
        <div class="payment-content" >

          <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="account-ruleForm" size="medium">

            <el-form-item label="" :label-width="formLabelWidth" prop="paymentNum">
              <el-input v-model="ruleForm.paymentNum" auto-complete="off" placeholder="" style="width: 330px;height:40px;"></el-input>
            </el-form-item>

            <el-form-item label="" :label-width="formLabelWidth" prop="enterpriseName">
              <el-input v-model="ruleForm.enterpriseName" auto-complete="off" placeholder="" disabled="disabled" style="width: 330px;height:40px;"></el-input>
            </el-form-item>

            <el-form-item label="" :label-width="formLabelWidth" prop="bankAccount">
              <el-input v-model="ruleForm.bankAccount" auto-complete="off" placeholder="" style="width: 330px;height:40px;" disabled="disabled"></el-input>
            </el-form-item>

            <el-form-item label="" :label-width="formLabelWidth" prop="bankName">
              <el-input v-model="ruleForm.bankName" auto-complete="off" placeholder="" :maxlength= "30" style="width: 330px;height:40px;" disabled="disabled"></el-input>
            </el-form-item>


            <el-form-item label="" :label-width="formLabelWidth" prop="bankArea">

              <el-input placeholder=""  v-model="ruleForm.bankArea" style="width: 330px;height:40px;" disabled="disabled"></el-input>
            </el-form-item>

            <el-form-item label="" :label-width="formLabelWidth" prop="bankBranchName">
              <el-input v-model="ruleForm.bankBranchName" auto-complete="off" placeholder="" :maxlength= "30" style="width: 330px;height:40px;" disabled="disabled"></el-input>
            </el-form-item>

          </el-form>

          <div class="edit-btn" >
            <el-button type="info" style="width:200px;height: 40px;" @click="cancel">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</el-button>

            <el-button type="primary" style="width: 200px;height: 40px;" @click="submit" :disabled="once"></el-button>
          </div>

          <el-dialog
            :visible.sync="dialogAgreement"
            :close-on-click-modal=false
            width="400px"
            center>

            <div  class="send-code" style="color: red;text-align: center;">
              <br/>
              </div>
            <!--<div  class="send-code"></div>-->
            <!--<div style="color: #333;text-align: left;padding-bottom: 10px;font-weight: bold;">{{mobileShowFirst}}&nbsp;<sub >****</sub>&nbsp;{{mobileShowLast}}</div>-->
            <el-form :model="legalForm" :rules="ruleFormRules" ref="legalForm" class="code-ruleForm">


              <el-form-item label="" prop="legalPerson">
                <el-input type="text" placeholder=""  v-model="legalForm.legalPerson" style="width: 250px;" disabled="disabled"></el-input>
              </el-form-item>


              <el-form-item prop="IDcard" label="" >
                <el-input type="text" placeholder=""  v-model="legalForm.IDcard" style="width: 250px;"></el-input>
              </el-form-item>

              <el-form-item prop="legalMobile" label="">
                <el-input type="text" placeholder=""  v-model="legalForm.legalMobile" style="width: 250px;" :maxlength= 11 :minlength= 11></el-input>

              </el-form-item>

              <el-form-item prop="phoneCode" label="">
                <el-input type="text" placeholder="" v-model="legalForm.phoneCode" style="width:165px;margin-left: 15px" :maxlength= 6 :minlength= 6></el-input>
                <el-button type="primary" class="forget-messageButton" @click="sendCode" id="code" style="margin-left: 10px;" :disabled="repeat"></el-button>
              </el-form-item>

              <div class="forget-btn" style="margin-top: 20px;margin-left: 62px;">
                <el-button type="primary" @click="thaw('legalForm')" style="width: 235px;" :disabled="verifySub">&nbsp;&nbsp;</el-button>
              </div>

            </el-form>
          </el-dialog>

        </div>
        <input type="hidden" v-model="time">

      </div>

    </div>
  </div>

</template>
<script>
  import server from '@/api/url.js'
  import cookie from '@/common/js/getTenant'
  import {validateOpenName,TrimAll,validateDecimal,onlyChinese,validateCard,validateMoblie,validateSmsCode} from '@/common/js/validate'
  export default {
    name: 'EnterprisePayments',

    data () {
      //
      var validatePaymentNum=(rule,value,callback)=>{
        if (value === '') {
          callback(new Error(''));
        }else if(TrimAll(value).length>4||TrimAll(value)<0.01||TrimAll(value)>0.99){
          callback(new Error('0.01~0.99'));
        }
      }

      // 
      var validateIDcard=(rule,value,callback)=>{
        if(value===''){
          callback(new Error(''))
        }else if(value!==''&&!(validateCard(TrimAll(value)))){
          callback(new Error(''));
        }else {
          callback()
        }
      }
      //
      var validateLegalMobile=(rule,value,callback)=>{
        if(value===''){
          callback(new Error(''))
        }else if(value!==''&&!(validateMoblie(TrimAll(value)))){
          callback(new Error(''));
        }else {
          callback()
        }
      }

      //6
      var validatePhoneCode=(rule,value,callback)=>{
        if(value===''){
          callback(new Error(''))
        }else if(value!==''&&!(validateSmsCode(value))){
          callback(new Error('6'));
        }else {
          callback()
        }
      }

      return {
        ruleForm: {
          paymentNum:'',  //
          enterpriseName:'',//
          bankAccount:'', //
          bankName:'' ,//
          bankArea:'', //
          bankBranchName:'',  //
        },
        message:'',
        // mobile:sessionStorage.getItem("mobile"),
        mobileShowFirst:'',
        mobileShowLast:'',
        dialogAgreement:false,
        once:false, //,
        verifySub:false, //
        // showAlert:true,  //  
        time:1,
        // allowInput:false,// 
        rules:{
          paymentNum: [
            {validator: validatePaymentNum, trigger: 'blur' }
          ],

        },
        legalForm:{
          legalPerson:'',
          IDcard:'',
          legalMobile:'',
          phoneCode:'',
        },
        ruleFormRules:{
          //   legalPerson:[
          //     { required: true,validator: validateLegalPerson, trigger: 'blur' }
          //   ],
          IDcard:[
            { required: true,validator: validateIDcard, trigger: 'blur' }
          ],
          legalMobile:[
            { required: true,validator: validateLegalMobile, trigger: 'blur' }
          ],
          phoneCode:[
            { required: true,validator: validatePhoneCode, trigger: 'blur' }
          ],
        },
        interfaceCode:cookie.getJSON("tenant")?cookie.getJSON("tenant")[1].interfaceCode:'',
        formLabelWidth:'200px',
        smsCode:'',   //
        smsNoVer:'',   //
        appId:'',  //appId
        repeat:false,
        pollTimer:null   //

      }
    },
    beforeDestroy() {
      clearInterval(this.timer);
      this.timer = null;
    },
    methods:{
      change (val) {
        this.province=val[0]
        this.city=val[1]
      },

      sendCode(){
        let mobile=this.legalForm.legalMobile;
        if(!mobile){
          this.$message({
            showClose: true,
            message: '',
            type: 'error'
          })
        }else{
          this.repeat = true;

          var codeType = '0';
          var count = 60;
          var curCount = count;
          var timer = null;
          this.sms = true;
          this.$http.post(process.env.API_HOST+'v1.4/sms/sendCode',{'mobile':mobile,'sendType':codeType,'interfaceCode':this.interfaceCode}, {emulateJSON: true}).then(function (res) {

            this.smsNoVer=res.data.smsNo   //
            this.appId=res.data.appId     //appId


            var resultCode = res.data.resultCode;
            var smsNo = res.data.smsNo;
            var smsCode = res.data.smsCode;
            var message = res.data.resultMessage;
            if (resultCode === '1') {
              this.smsNo = true;
              this.smsCodeNum +=1;
              if(this.smsCodeNum == 3){
                this.isDisabled = true
              } else{
                this.isDisabled = false
              }
              let codeInfo = document.getElementById('code')
              codeInfo.innerText =  curCount + ''
              this.smsNum = smsNo

              codeInfo.setAttribute('disabled', 'true')
              let that = this
              timer = setInterval(function () {
                codeInfo.innerText =  (curCount - 1) + ''
                if (curCount === 0) {
                  codeInfo.innerText = ''
                  clearInterval(timer)
                  codeInfo.removeAttribute('disabled')
                  that.repeat = false
                } else {
                  curCount--
                }
              }, 1000)
            }else{
              let that =this;

              that.smsNo = false
              that.repeat = false

              this.$alert(res.data.resultMessage,'', {
                confirmButtonText: ''
              })

            }
          })

        }

      },
      // 
      thaw(legalForm){

        this.$refs[legalForm].validate((valid) => {
          this.verifySub = true;
          if(valid){
            // console.log('this.ruleForm.phoneCode'+this.ruleFormRules.phoneCode)
            this.$http.get(process.env.API_HOST + 'v1.4/sms', {
              params: {
                'mobile': this.legalForm.legalMobile, 'smsNo': this.smsNoVer, 'smsCode': this.legalForm.phoneCode, 'appId': this.appId
              }
            }).then(res => {
              //
              if (res.data.resultCode != 1) {
                this.verifySub = false
                this.$message({
                  showClose: true,
                  message: res.data.resultMessage,
                  type: 'error'
                })
              }else {
                //
                this.verifySub = false;
                // this.$message({
                //     showClose: true,
                //     message: res.data.resultMessage,
                //     type: 'success'
                // })

                //

                let param ={
                  'userName':this.legalForm.legalPerson,
                  'idCard':this.legalForm.IDcard,
                  'mobile':this.legalForm.legalMobile,
                  'interfaceCode':this.interfaceCode,
                };
                // console.log(param);
                let that=this;
                server.unfreezeRemittance(param).then(function (res) {
                  if (res.data.resultCode == '1') {
                    that.verifySub = false
                    that.$message({
                      showClose: true,
                      message: res.data.resultMessage,
                      type: 'success'
                    })
                    that.dialogAgreement=false;
                    that.ruleForm.paymentNum='';
                    that.legalForm.IDcard='';
                    that.legalForm.legalMobile='';
                    that.legalForm.phoneCode='';
                  } else {
                    that.$message({
                      showClose: true,
                      message: res.data.resultMessage,
                      type: 'error'
                    })
                    that.dialogAgreement=false;

                  }

                })

              }


            })
          }
        })
      },
      cancel(){
        this.$store.dispatch('tabIndex',{tabIndex:0});
        this.$router.push('/Merchant')
      },
      //
      submit(){
          if(this.ruleForm.paymentNum==''){
            this.$alert('', '',{
              confirmButtonText: ''
            });
            return false
          }else if(this.ruleForm.paymentNum<0.01||this.ruleForm.paymentNum>0.99||this.ruleForm.paymentNum.length>4){
            this.$alert('0.01~0.99', '',{
              confirmButtonText: ''
            });
            return false
          }else{
            this.once=true;
            let param ={'trans_money':this.ruleForm.paymentNum};
            server.verifyRemittance(param,this.interfaceCode).then(res => {
              if (res.data.resultCode == 0) {
                this.$message({
                  showClose: true,
                  message:res.data.resultMessage,
                  type: 'error'
                })
                this.once=false;
              } else if (res.data.resultCode == 1) {
                this.once=false;
                this.$alert(res.data.resultMessage, '', {
                  confirmButtonText: ''
                }).then(() => {
                    this.$router.push('/EnterpriseRegisterSucc')
                })
                this.once=false;
              } else if(res.data.resultCode == '-4'){
                this.once=false;
                this.message=res.data.resultMessage;
                this.legalForm.legalPerson=res.data.data;
                this.dialogAgreement=true;

              }else{
                this.once=false;
                this.$message({
                  showClose: true,
                  message:res.data.resultMessage,
                  type: 'error'
                })
              }

            })
          }


      },

      //
      pollingPanel(timer){ //
        let that = this;
        server.moneyStatus(this.interfaceCode).then(function (res) {
          if(res.data.resultCode=='1') {
            clearInterval(that.timer);
            that.timer = null;
          }else if(res.data.resultCode=='-4'){
            if(that.time>(1000*60*60)){
              clearInterval(that.timer);
              that.timer = null;
            }
          }else if(res.data.resultCode=='-1'){
            clearInterval(that.timer);
            that.timer = null;
            that.$alert(res.data.resultMessage, '',{
                confirmButtonText: ''
              }).then(()=>{
                   that.$router.push('/EnterpriseCertificate')
              });
          }

        })
      },

    },
    created() {
      if (cookie.getJSON("tenant")[1].auditSteps == 3) {
        this.$router.push("/Home")
        return
      }
      this.enterpriseName = sessionStorage.getItem("companyName");
      //   console.log(this.interfaceCode);
      // 
      server.getBank(this.interfaceCode).then(response => {
        if (response.data.resultCode == '1') {
          this.ruleForm.bankAccount = response.data.data.to_acc_no; //
          this.ruleForm.bankName = response.data.data.to_bank_name; //
          this.ruleForm.enterpriseName = response.data.data.to_acc_name; //
          this.ruleForm.bankArea = response.data.data.to_pro_name + response.data.data.to_city_name; //
          this.ruleForm.bankBranchName = response.data.data.to_acc_dept; //

        } else if (response.data.resultCode == '0') {
          this.$message({
            showClose: true,
            message: response.data.resultMessage,
            type: 'error'
          })
        }
      })

      // 
      let that = this;
      let timer = null;
      this.timer = setInterval(function () {
        that.pollingPanel(this.timer)
      }, 3000);

      setInterval(function () {
        that.time=that.time+1;
      },1000)

    }

  }
</script>

<style lang="stylus">
  @import "../../../common/styles/Topes.css";
  @import "../../../common/styles/SigningSteps.css";
  @import "../../../styles/EnterpriseCertificate/EnterprisePayment.styl";
  .send-code{
    margin: 15px 0;
    font-size: 14px;
    color: #666;
  }
  .code-ruleForm{
    width: 500px;
    height: 300px;
  }
</style>



