import Vue from 'vue'
import axios from 'axios';


const Axios = axios.create({
  // baseURL: process.env.BASE_API,    // apibase_url
  timeout: 5000,                    // request timeout
  responseType: "json",
//   withCredentials: true,            // cookie
})


// 
Axios.interceptors.request.use((config)=>{
    //
    return config;
  },(error)=>{
      //
    return Promise.reject(error);
  });

// 
Axios.interceptors.response.use((response)=> {
    //
    return response;
  },(error)=> {
    if (error && error.response) {
      switch (error.response.status) {
        case 400:
          error.message = ''
          break;
        case 401:
          error.message = ''
          break;
        case 403:
          error.message = ''
          break;
        case 404:
          error.message = ','
          break;
        case 405:
          error.message = ''
          break;
        case 408:
          error.message = ''
          break;
        case 500:
          error.message = ''
          break;
        case 501:
          error.message = ''
          break;
        case 502:
          error.message = ''
          break;
        case 503:
          error.message = ''
          break;
        case 504:
          error.message = ''
          break;
        case 505:
          error.message = 'http'
          break;
        default:
          error.message = `${error.response.status}`
      }
    } else {
      error.message = ""
    }
    Vue.prototype.$message({
      showClose: true,
      duration:1000,
      message: error.message,
      type: 'warning'
    })
    return Promise.reject(error);
  });

export default Axios;




