// next  resolved;
//metaauth 
//this.$message  vue

router.beforeEach((to, from, next) => {
	var userInfo = cookie.getJSON('tenant')
	if (to.meta.auth == true) {
		if (!userInfo) {
			Vue.prototype.$message({
				showClose: true,
				duration: 1000,
				message: "",
				type: 'warning'
			})
			setTimeout(function(){
				next({
					path: '/'
				}),9000
			})
		}else {
			next()
		}
	} else {
		next()
	}
})






